import React from 'react'
import { getFormattedCurrency } from 'utils/currency'
import { Link } from 'react-router-dom'
import './index.styl'

interface Props {
  car: {
    priceFrom: string,
    imageUrl: string,
    name: string,
    code: string,
  }
}

const MModel = (props: Props) => {
  return (
    <div className='MModel-root'>
      <Link to={`/models/${props.car.code}/trim`}>
        <img className='image' src={props.car.imageUrl} title={props.car.name} alt={props.car.name} />
        <div className='description'>
          <div className='name'>{props.car.name}</div>
          <div className='price'>{getFormattedCurrency(+props.car.priceFrom)}</div>
        </div>
      </Link>
    </div>
  )
}

export default MModel
