import { useSelector, useDispatch } from 'react-redux'
import { getFormattedCurrency } from 'utils/currency'
import { selectTrim } from 'modules/car/actions'
import { CarTrimColors } from './types'
import { Button } from 'components'
import React from 'react'
import './index.styl'

interface RootState {
  car: {
    selectedCarTrim: {
      name: string,
    },
    car: {
      trims: any[],
    }
  }
}

const getSelectedCarTrim = (state: RootState) => state.car.selectedCarTrim
const getCarModel = (state: RootState) => state.car.car

const SMEquipment = () => {
  const selectedCarTrim = useSelector(getSelectedCarTrim)
  const car = useSelector(getCarModel)
  const dispatch = useDispatch()

  const selectEquipment = (selectedTrim: CarTrimColors) => {
    dispatch(selectTrim(selectedTrim))
  }

  return (
    <div className='SMEquipment-root'>
      <div className='top'>
        <div className='header'>CHOOSE EQUIPMENT LEVEL</div>
        <div className='buttons'>
          {car.trims.map(i => (
            <Button
              active={i.name === selectedCarTrim.name}
              onClick={() => selectEquipment(i)}
              className='button'
              key={i.name}
            >
              {i.name}
              <div className='price'>{getFormattedCurrency(i.price)}</div>
            </Button>
          ))}
        </div>
      </div>
    </div>
  )
}

export default SMEquipment
