import { getFormattedCurrency } from 'utils/currency'
import SMPNavigation from './SMPNavigation'
import { useSelector } from 'react-redux'
import React from 'react'
import './index.styl'

interface CarTrimColor {
  imageUrl: string,
  iconUrl: string,
  name: string,
  price: number
}

interface RootState {
  car: {
    selectedCarTrim: {
      color: CarTrimColor,
      price: number,
      name: string,
    },
    car: {
      code: string,
      name: string,
      trims: any[],
    }
  }
}

const getSelectedCarTrim = (state: RootState) => state.car.selectedCarTrim
const getCarModel = (state: RootState) => state.car.car

const SMPreview = (props: any) => {
  const selectedCarTrim = useSelector(getSelectedCarTrim)
  const car = useSelector(getCarModel)

  return (
    <div className='SMPreview-root'>
      <div className='SMPreview-left'>
        <div className='info'>
          <div className='header'>
            <div className='model'>{car.name} </div>
            <div className='color'>{selectedCarTrim.name}</div>
          </div>
          <div className='label'>{selectedCarTrim.color.name}</div>
          <img
            src={selectedCarTrim.color.imageUrl}
            title={selectedCarTrim.color.name}
            alt={selectedCarTrim.color.name}
            className='image'
          />
        </div>
        <div className='price'>
          {getFormattedCurrency(selectedCarTrim.price + selectedCarTrim.color?.price)}
        </div>
      </div>
      <div className='SMPreview-right'>
        {props.children}
        <SMPNavigation />
      </div>
    </div>
  )
}

export default SMPreview
