import {
  CARS_GLOBAL_LOADING_FINISHED,
  GET_CAR_MODEL_BY_CODE,
  CARS_GLOBAL_LOADING,
  SelectedCarTrim,
  SELECTED_TRIM,
  CarTrimColors,
  CarTrimColor,
  GET_MODELS,
} from './types'
import { getCarModelByCode, carsLeadSelect, getCarsModels } from 'services/car.service'
import { ICar } from 'store/types'
import { Dispatch } from 'redux'

export function getModels () {
  // <AppActions>
  return async (dispatch: Dispatch) => {
    try {
      dispatch({ type: CARS_GLOBAL_LOADING })
      const cars = await getCarsModels()
      dispatch({
        type: GET_MODELS,
        payload: { cars },
      })
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    } catch (err) {
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    }
  }
}

export function getModelByCode (code: string) {
  return async (dispatch: Dispatch, getState: () => ICar) => {
    try {
      if (getState().car.car?.code === code) {
        return
      }
      dispatch({ type: CARS_GLOBAL_LOADING })
      const car = await getCarModelByCode(code)
      car.trims.sort((a: CarTrimColors, b: CarTrimColors) => a.price - b.price)
      car.trims.forEach((i: CarTrimColors) => {
        i.colors.sort((a: CarTrimColor, b: CarTrimColor) => a.price - b.price)
      })
      dispatch({
        type: GET_CAR_MODEL_BY_CODE,
        payload: { car },
      })
      dispatch<any>(selectTrim(car.trims?.[0]))
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    } catch (err) {
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    }
  }
}

export function selectTrim (carTrim: CarTrimColors) {
  return (dispatch: Dispatch) => {
    try {
      dispatch({ type: CARS_GLOBAL_LOADING })
      const selectedCarTrim = {
        price: carTrim.price || 0,
        colors: carTrim.colors,
        name: carTrim.name,
        color: {},
      }
      dispatch({
        payload: { selectedCarTrim },
        type: SELECTED_TRIM,
      })
      dispatch<any>(selectTrimColor(carTrim.colors?.[0]))
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    } catch (err) {
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    }
  }
}

export function selectTrimColor (carTrimColor: CarTrimColor) {
  return (dispatch: Dispatch, getState: () => ICar) => {
    try {
      dispatch({ type: CARS_GLOBAL_LOADING })
      const res = {
        ...getState().car.selectedCarTrim,
        color: carTrimColor,
      }
      dispatch({
        payload: { selectedCarTrim: res },
        type: SELECTED_TRIM,
      })
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    } catch (err) {
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
    }
  }
}

export function proceedCarSelect (carTrimColor: CarTrimColor) {
  return async (dispatch: Dispatch, getState: () => ICar) => {
    try {
      dispatch({ type: CARS_GLOBAL_LOADING })
      const selected = getState().car.selectedCarTrim
      const data: SelectedCarTrim = {
        modelName: getState().car.car.name,
        colorName: selected.color.name,
        trimName: selected.name,
      }
      const success = await carsLeadSelect(data)
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
      return success
    } catch (err) {
      dispatch({ type: CARS_GLOBAL_LOADING_FINISHED })
      console.error('error', err)
    }
  }
}
