import { useSelector, useDispatch } from 'react-redux'
import { selectTrimColor } from 'modules/car/actions'
import { getFormattedCurrency } from 'utils/currency'
import { CarTrimColor } from 'modules/car/types'
import cs from 'classnames'
import React from 'react'
import './index.styl'

interface RootState {
  car: {
    selectedCarTrim: {
      colors: CarTrimColor[],
    },
  }
}

const getSelectedCarTrim = (state: RootState) => state.car.selectedCarTrim

const SMColor = () => {
  const selectedCarTrim = useSelector(getSelectedCarTrim)
  const dispatch = useDispatch()

  const selectEquipment = (selectedTrimColor: CarTrimColor) => {
    dispatch(selectTrimColor(selectedTrimColor))
  }

  return (
    <div className='SMColor-root'>
      <div className='top'>
        <div className='header'>SELECT COLOR</div>
        <div className='mid'>
          <div className='colors'>
            {selectedCarTrim.colors.map(i => (
              <div
                onClick={() => selectEquipment(i)}
                className={cs('color', { 'active-color': i.name === selectedCarTrim.color.name })}
                key={i.name}
              >
                <div className='circle-image'>
                  <img className='image' src={i.iconUrl} title={i.name} alt={i.name} />
                </div>
                <div className='name'>{i.name}</div>
                <div className='price'>+{getFormattedCurrency(i.price)}</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default SMColor
