import { useHistory, useParams, Link } from 'react-router-dom'
import { proceedCarSelect } from 'modules/car/actions'
import arrowRight from '~/assets/arrowRight.svg'
import arrowLeft from '~/assets/arrowLeft.svg'
import { useDispatch } from 'react-redux'
import { Button } from 'components'
import React from 'react'
import './index.styl'

const SMPNavigation = () => {
  const history = useHistory()
  const params = useParams() || {}
  const path = location.pathname.includes('/color')
    ? `${params.code}/trim`
    : ''

  const dispatch = useDispatch()

  const proceed = async () => {
    const success = await dispatch(proceedCarSelect())
    history.push(`/checkout/${success ? 'success' : 'failure'}`)
  }

  return (
    <div className='SMPNavigation-root'>
      <Link to={`/models/${path}`} className='left-button'>
        <Button className='arrow-left'>
          <img src={arrowLeft} alt='ArrowLeftOutlined' />
        </Button>
      </Link>
      {path ? (
        <div className='mid-button'>
          <Button type='primary' className='proceed' onClick={proceed}>
            PROCEED
          </Button>
        </div>
      ) : (
        <Link to={`/models/${params.code}/color`}>
          <Button type='primary' className='arrow-right'>
            <img src={arrowRight} alt='ArrowRightOutlined' />
          </Button>
        </Link>
      )}
    </div>
  )
}

export default SMPNavigation
