import { Button as Btn } from 'antd'
import cs from 'classnames'
import React from 'react'
import './index.styl'

const Button = (props: any) => {
  const classes = cs(
    props.active ? 'button-active' : 'button-not-active',
    props.className || '',
    'button-default',
  )

  return (
    <div className='Button-root'>
      <Btn {...props} className={classes}>
        {props.children}
      </Btn>
    </div>
  )
}

export default Button
