const getUrl = url => `https://reacttestprojectapi.azurewebsites.net/${url}`

const makeRequest = async (path = '', options = {}, json = true, text, isHeaders = false) => {
  options = {
    headers: {
      'X-API-KEY': '61fe14cf-75ea-4d1a-a8ed-e79c89ac1646',
      Accept: 'application/json',
    },
    method: 'GET',
    ...options,
  }

  const response = await fetch(getUrl(path), options)

  if (!response.ok) {
    throw new Error('Req failed')
  }

  if (json) return response.json()

  return response
}

const makePostRequest = async (path, data, responseAsJson = false) => {
  const options = {
    headers: {
      'X-API-KEY': '61fe14cf-75ea-4d1a-a8ed-e79c89ac1646',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data || {}),
    method: 'POST',
  }
  return await makeRequest(path, options, responseAsJson)
}

export { makeRequest, makePostRequest }
