import {
  CARS_GLOBAL_LOADING_FINISHED,
  GET_CAR_MODEL_BY_CODE,
  CARS_GLOBAL_LOADING,
  ModelsActionTypes,
  SELECTED_TRIM,
  ModelState,
  GET_MODELS,
} from './types'

const initialState: ModelState = {
  globalLoading: false,
  selectedCarTrim: {
    colors: [],
    color: {
      imageUrl: '',
      iconUrl: '',
      name: '',
      price: 0,
    },
    price: 0,
    name: '',
  },
  car: {
    trims: [],
    code: '',
    name: '',
  },
  cars: [],
}

export function carReducer (
  state = initialState,
  action: ModelsActionTypes,
): ModelState {
  switch (action.type) {
  case CARS_GLOBAL_LOADING_FINISHED:
    return { ...state, globalLoading: false }
  case CARS_GLOBAL_LOADING:
    return { ...state, globalLoading: true }
  case GET_MODELS:
    return { ...state, cars: action.payload.cars }
  case GET_CAR_MODEL_BY_CODE:
    return { ...state, car: action.payload.car }
  case SELECTED_TRIM:
    return { ...state, selectedCarTrim: action.payload.selectedCarTrim }
  default:
    return state
  }
}
