export interface Car {
  priceFrom: string,
  imageUrl: string,
  name: string,
  code: string,
}

export interface CarTrimColor {
  imageUrl: string,
  iconUrl: string,
  name: string,
  price: number
}

export interface CarTrim {
  colors: CarTrimColor[],
  color: CarTrimColor,
  price: number,
  name: string,
}

export interface SelectedCarTrim {
  modelName: string,
  colorName: string,
  trimName: string,
}

export interface CarTrimColors {
  colors: CarTrimColor[],
  price: number,
  name: string,
}

export interface CarModel {
  trims: CarTrim[],
  name: string,
  code: string,
}

export interface ModelState {
  selectedCarTrim: CarTrim,
  globalLoading: boolean,
  car: CarModel,
  cars: Car[],
}

export const CARS_GLOBAL_LOADING_FINISHED = 'CARS_GLOBAL_LOADING_FINISHED'
export const GET_CAR_MODEL_BY_CODE = 'GET_CAR_MODEL_BY_CODE'
export const CARS_GLOBAL_LOADING = 'CARS_GLOBAL_LOADING'
export const SELECTED_TRIM = 'SELECTED_TRIM'
export const GET_MODELS = 'GET_MODELS'

interface GetModelsAction {
  type: typeof GET_MODELS,
  payload: ModelState,
}

interface GetCarModelByCodeAction {
  type: typeof GET_CAR_MODEL_BY_CODE,
  payload: ModelState,
}

interface SelectTrim {
  type: typeof SELECTED_TRIM,
  payload: ModelState,
}

interface SetGlobalLoadingFinished {
  type: typeof CARS_GLOBAL_LOADING_FINISHED,
}

interface SetGlobalLoading {
  type: typeof CARS_GLOBAL_LOADING,
}

export type ModelsActionTypes = SetGlobalLoadingFinished | GetCarModelByCodeAction | GetModelsAction | SetGlobalLoading | SelectTrim
