import { makePostRequest, makeRequest } from './baseService'
import { SelectedCarTrim } from '../modules/car/types'

export const getCarsModels = () => makeRequest('Cars/Models')

export const getCarModelByCode = (code: string) => makeRequest(`Cars/Model/${code}`)

export const carsLeadSelect = (carsLead: SelectedCarTrim) =>
  makePostRequest('/Cars/lead', carsLead, false)
    .then(res => res.status === 204)
    .catch(err => {
      console.error(err)
      return false
    })
