import { getModelByCode } from 'modules/car/actions'
import { useParams } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import React, { useEffect } from 'react'
import SMEquipment from './SMEquipment'
import SMPreview from './SMPreview'
import SMColor from './SMColor'
import './index.styl'

const SingleModel = () => {
  const dispatch = useDispatch()
  const params = useParams()

  useEffect(() => {
    dispatch(getModelByCode(params.code))
  }, [])

  return (
    <div className='SingleModel-root'>
      <SMPreview>
        {location.pathname.includes('/trim') ? <SMEquipment /> : <SMColor />}
      </SMPreview>
    </div>
  )
}

export default SingleModel
