import success from '~/assets/success.svg'
import fail from '~/assets/fail.svg'
import React from 'react'
import './index.styl'

const Checkout = () => {
  const obj = {
    success,
    fail,
  }
  const status = location.pathname.includes('success') ? 'success' : 'fail'
  return (
    <div className='Checkout-root'>
      <img src={obj[status]} alt={status} />
    </div>
  )
}

export default Checkout
