import { SingleModel, Checkout, Models } from 'pages'

export const baseRoutes = [
  {
    path: '/models',
    component: Models,
    exact: true,
  },
  {
    path: '/models/:code/trim',
    component: SingleModel,
  },
  {
    path: '/models/:code/color',
    component: SingleModel,
  },
  {
    path: '/checkout/success',
    component: Checkout,
  },
  {
    path: '/checkout/failure',
    component: Checkout,
  },
]
