import { useSelector, useDispatch } from 'react-redux'
import { getModels } from 'modules/car/actions'
import React, { useEffect } from 'react'
import MModel from './MModel'
import './index.styl'

// interface Props {
// messages: { [locale: string]: string }
// }

interface RootState {
  car: {
    cars: {
      priceFrom: string,
      imageUrl: string,
      name: string,
      code: string,
    }
  }
}

const getCars = (state: RootState) => state.car.cars

const Models = (props: RootState) => {
  const cars = useSelector(getCars)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getModels())
  }, [])

  return (
    <div className='Models-root'>
      <div className='header'>CHOOSE YOUR NEW CAR</div>
      <div className='cars'>
        {cars.map(i => <MModel car={i} key={i.code} />)}
      </div>
    </div>
  )
}

export default Models
